.container {
    display: flex;
    flex-direction: column;
}

.video_container {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.img_button_container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.video_pics {
    display: flex;
    justify-content: space-around;
}

.button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: rgba(100, 100, 100, 1);
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: 'Old Standard TT', serif;
}

.button:hover {
    background-color: rgba(200, 200, 200, 1);
}

.image {
    max-width: 500px;
    height: 300px;
    border: 5px solid rgba(255, 255, 255, 0.5);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.3);
    background-color: rgba(0, 0, 0, 0.9);
}

.title_container {
    text-align: center;
    color: olive;
}

.video {
    border: 5px solid rgba(255, 255, 255, 0.5);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 1200px) {

    .video_container,
    .img_button_container {
        width: 100%;
    }

    .video_pics {
        flex-wrap: wrap;
        justify-content: center;
    }

    .image {
        max-width: 100%;
        height: 300px;
    }

    h2 {
        font-size: 18px;
    }

    .video {
        width: 100%;
        max-width: 400px;
        height: 300px;
        border: 5px solid rgba(255, 255, 255, 0.5);
        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.3);
    }
}